import {
  React,
  useState,
  Table,
  Modal,
  Button,
  connect,
  _,
} from "../../../global";
import UserForm from "../forms/user/UserForm";

const UsersTable = (props) => {
  console.log("props sa tabele", props);
  const [isVisibleModal, setIsVisibleModal] = useState(false);
  const [useCurentUser, setCurentUser] = useState("");

  const dataSource = props.adminUsers.map((user, index) => ({
    key: index,
    userId: user.userId,
    firstName: user.firstName,
    lastName: user.lastName,
    userName: user.userName,
    email: user.email,
    phone: user.phone,
    address: user.address,
    delete: user.delete,
  }));

  const columns = [
    {
      title: "ID",
      dataIndex: "userId",
      key: "userId",
    },
    {
      title: "Ime",
      dataIndex: "firstName",
      key: "firstName",
    },
    {
      title: "Prezime",
      dataIndex: "lastName",
      key: "lastName",
    },
    {
      title: "Korisnicko ime",
      dataIndex: "userName",
      key: "userName",
    },
    {
      title: "Email",
      dataIndex: "email",
      key: "email",
    },
    {
      title: "Telefon",
      dataIndex: "phone",
      key: "phone",
    },
    {
      title: "Uredi",
      dataIndex: "Edit",
      key: "Edit",
      render: (text, data) => (
        <Button type="primary" onClick={() => handler(data)}>
          Edit
        </Button>
      ),
    },
    {
      title: "Obrisi",
      dataIndex: "Delete",
      key: "Delete",
      render: () => (
        <Button type="primary" danger>
          Delete
        </Button>
      ),
    },
  ];

  const handler = (userForEdit) => {
    setIsVisibleModal(true);
    setCurentUser(userForEdit);
  };

  // const showModal = () => {
  //   setIsVisibleModal(true);
  // };

  const tableData = _.orderBy(dataSource, "id", "asc");

  return (
    <div>
      <Table
        pagination={{ pageSize: 10 }}
        dataSource={tableData}
        columns={columns}
      />
      <Modal
        title={"test modal"}
        visible={isVisibleModal}
        onOk={() => setIsVisibleModal(false)}
        onCancel={() => setIsVisibleModal(false)}
        width={1300}
      >
        <UserForm user={useCurentUser} />
      </Modal>
    </div>
  );
};

const mapStateToProps = (state) => {
  const userAdminState = state;
  console.log("STATE", userAdminState);
  // Redux Store --> Component
  return {
    adminUsers: userAdminState.adminUsers.data,
    curentUser: userAdminState.adminUsers.currentUser,
  };
};

// export default UsersTable;
export default connect(mapStateToProps)(UsersTable);
